import { VerticalView } from './vertical-view';
import * as util from '../base/util';
/**
 * work week view
 */
export class WorkWeek extends VerticalView {
    /**
     * Constructor for work week view
     *
     * @param {Schedule} parent Accepts the schedule instance
     */
    constructor(parent) {
        super(parent);
        this.viewClass = 'e-work-week-view';
    }
    startDate() {
        let startDate = this.renderDates[0];
        if (this.parent.activeViewOptions.group.resources.length > 0) {
            startDate = this.parent.resourceBase.getResourceRenderDates()[0];
        }
        return startDate;
    }
    endDate() {
        let endDate = util.addDays(this.renderDates[this.renderDates.length - 1], 1);
        if (this.parent.activeViewOptions.group.resources.length > 0) {
            endDate = util.addDays(this.parent.resourceBase.getResourceRenderDates().slice(-1)[0], 1);
        }
        return endDate;
    }
    /**
     * Get module name.
     *
     * @returns {string} Returns the module name.
     */
    getModuleName() {
        return 'workWeek';
    }
}
